import request from '@/utils/request'
// 门店接口
// 获取列表
export function fetchList (query) {
  return request({
    url: '/cardmas',
    method: 'Post',
    params: query
  })
}
// 新增
export function add (data) {
  return request({
    url: '/cardmas/add',
    method: 'Post',
    data
  })
}
// 通过id获取详细信息
export function detail (id) {
  return request({
    url: '/cardmas/detail',
    method: 'Post',
    id
  })
}
// 修改
export function update (data) {
  return request({
    url: '/cardmas/update',
    method: 'post',
    data
  })
}
// 删除
export function Delete (id) {
  return request({
    url: '/cardmas/delete',
    method: 'post',
    id
  })
}



