<template>
      <el-dialog title="公司详细信息" :visible.sync="visible" width="800px">
            <el-form ref="form" :model="company" label-width="150px" margin-right="100px">
                <el-form-item label="公司编号(不可修改)">
                    <el-input v-model="company.rowid" :disabled='true'></el-input>
                </el-form-item>
                <el-form-item label="公司名称">
                    <el-input v-model="company.cname"></el-input>
                </el-form-item>
                <el-form-item label="公司电话">
                    <el-input v-model="company.tel"></el-input>
                </el-form-item>
                <el-form-item label="公司地址">
                    <el-input v-model="company.address"></el-input>
                </el-form-item>
                <el-form-item label="备注信息">
                    <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 6}" placeholder="请输入内容" v-model="company.remark">
                    </el-input>
                </el-form-item>

                <el-row>
                    <el-col :span="12">
                        <el-form-item label="创建者">
                            <el-input v-model="company.creatid" :disabled='true'></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="创建日期">
                            <el-date-picker label="创建日期" v-model="company.creatdate" :disabled='true'>
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="修改者">
                            <el-input v-model="company.updaid" :disabled='true'></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="修改日期">
                            <el-date-picker v-model="company.updadate" :disabled='true'></el-date-picker>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialog = false">取 消</el-button>
                <el-button type="primary" @click="updata">确 定</el-button>
            </span>
        </el-dialog>

</template>

<script>
export default {
  data: function () {
    return {
      visible: false
    }
  }

}
</script>

<style>

</style>



