<template>
  <div class="card">
    <img :src='back_img'>
    <p>{{num}}</p>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      back_img: ''
    }
  },
  created () {
    var a = Math.ceil(Math.random() * 3)
    this.back_img = require('@/assets/cardsbg/c' + a + '.jpg')
  },
  props: ['num', 'state']
}
</script>

<style scoped>
.card {
  text-align: center;
  width: 300px;
  height: 200px;
  padding: 30px;
  cursor: pointer;
}
.card > img {
  width: 300px;
  height: 200px;
  border-radius: 20px;
}
.card > p {
  width: 300px;
  height: 200px;
  margin-top: -150px;
  font-size: 1.8em;
  color:white;
}
</style>



