// 卡种
<template>
  <div class="table">
    <div class="container">
      <div class="handle-box">
        <el-button type="success" icon="search" @click="addClick">新增</el-button>
      </div>

      <el-row>
        <el-col :span="8" v-for="(card,index) in list" :key="index">
          <a @click="fetchList(index)">
            <card :num='card.cname'></card>
          </a>
        </el-col>
      </el-row>

      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" @prev-click='prevClick' @next-click='nextClick' background layout="total, sizes, prev, pager, next, jumper" :total='total' :page-sizes="[10,15,20,50]" :page-size="size" :current-page="pageNum">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>import { fetchList } from '@/api/company/cardmas.js'
import addcard from './addcard'
import card from './card'

var _ = require('lodash')

export default {
  components: {
    addcard,
    card
  },
  data () {
    return {
      listQuery: {
        page: 0, // 当前页
        size: 10 // 一页多少
      },
      listLoading: true,
      list: [],
      pageNum: 0, // 当前页
      pages: 0, // 总页数
      pageSize: 0, // 每页的数量
      size: 0, // 当前页的数量
      total: 0 // 总记录数
    }
  },
  created () {
    this.getList()
  },
  methods: {
    // 双击添加
    addClick () {
      this.$refs.addstore.visible = true
    },
    // 双击弹出
    handleDoubleClick (row, event) {
      console.log(row)
      this.$refs.editstore.visible = true
      let store = _.cloneDeep(row)
      this.$refs.editstore.store = store
      // this.$refs.editstore.setCity()
    },
    //   编辑信息弹出
    handleClick (store) {
      this.editDialog = true
      this.store = store
    },
    // 获取列表
    getList () {
      this.listLoading = fetchList(this.listQuery)
        .then(response => {
          this.list = response.data.list
          this.pages = response.data.pages
          this.pageSize = response.data.pageSize
          this.total = response.data.total
          this.pageNum = response.data.pageNum
          console.log('获取列表')
          console.log(response)
          this.listLoading = false
          // this.total = response.data.data.total
          // setTimeout(() => {
          //   this.listLoading = false
          // }, 1.5 * 1000)
        })
        .catch(error => {
          console.log('组件内接受错误' + error)
          this.listLoading = false
        })
    },
    // 改变每一页多少条数据
    handleSizeChange (val) {
      this.listQuery.size = val
      this.getList()
      console.log(`每页 ${val} 条`)
    },
    // 改变当前页
    handleCurrentChange (val) {
      this.listQuery.page = val
      this.getList()
      console.log(`当前页: ${val}`)
    },
    // 上一页
    prevClick () {
      ++this.listQuery.size
      this.getList()
    }, // 下一页
    nextClick () {
      --this.listQuery.size
      this.getList()
    }
  }
}
</script>

<style scoped>
.handle-box {
  margin-bottom: 10px;
}
.handle-select {
  width: 120px;
}
.handle-input {
  width: 300px;
  display: inline-block;
}
.del-dialog-cnt {
  font-size: 16px;
  text-align: center;
}
.table {
  width: 100%;
  font-size: 14px;
}
.container {
  background: url("../../../../assets/contentbg.png") repeat;
}
</style>



